.navigation {
  width: 100vw;
  height: 15vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  z-index: 100;
  top: 0; }
  .navigation__logo {
    width: 50%; }
    .navigation__logo > img {
      cursor: pointer;
      width: 40%;
      height: 40%; }
  .navigation__links {
    list-style-type: none;
    display: flex; }
    .navigation__links__link {
      padding: 1.5rem;
      font-size: 1.5rem;
      cursor: pointer; }

.home {
  position: relative;
  width: 100%;
  height: 100vh;
  background: url("../img/home.jpg") no-repeat;
  background-attachment: fixed; }
  .home__box {
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%); }
    .home__box > section {
      width: 100%;
      height: 100%; }
      .home__box > section > span {
        font-size: 3rem;
        color: #fff; }
    .home__box__title {
      text-align: center;
      color: #fff;
      font-size: 4rem;
      line-height: 0;
      font-weight: 400; }
    .home__box__btn {
      position: absolute;
      bottom: -7rem;
      left: 50%;
      transform: translateX(-50%);
      font-size: 2rem;
      text-align: center;
      width: 15rem;
      height: 2.5rem;
      padding: 0.5rem;
      background: #fff;
      border-radius: 2rem;
      cursor: pointer; }

.about {
  width: 100vw;
  height: 75vh;
  margin: 0 20vw 0 20vw; }
  .about__title > h3 {
    position: relative;
    color: #000;
    font-size: 4rem;
    line-height: 0; }
    .about__title > h3::before {
      content: "O nas";
      color: #83b748;
      position: absolute;
      left: 0;
      bottom: -1rem;
      font-size: 6rem;
      opacity: 50%;
      z-index: -1; }
  .about__grid {
    margin: 10rem 35vw 0 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr); }
    .about__grid__box {
      text-align: center; }
      .about__grid__box > i {
        background: #83b748;
        border-radius: 50%;
        padding: 3rem;
        font-size: 2rem; }
      .about__grid__box > h4 {
        font-size: 2rem;
        line-height: 0; }

.program {
  position: relative;
  width: 100vw;
  height: 75vh;
  margin: 0 20vw 0 20vw; }
  .program__title > h3 {
    position: relative;
    color: #000;
    font-size: 4rem;
    line-height: 0; }
    .program__title > h3::before {
      content: "Program";
      color: #83b748;
      position: absolute;
      left: 0;
      bottom: -1rem;
      font-size: 6rem;
      opacity: 50%;
      z-index: -1; }
  .program__timeline {
    position: absolute;
    left: 30%;
    top: 60%;
    transform: translate(-50%, -50%);
    width: 30%; }

.organizers {
  width: 100vw;
  height: 75vh;
  margin: 0 20vw 0 20vw; }
  .organizers__title > h3 {
    position: relative;
    color: #000;
    font-size: 4rem;
    line-height: 0; }
    .organizers__title > h3::before {
      content: "Organizatorzy";
      color: #83b748;
      position: absolute;
      left: 0;
      bottom: -1rem;
      font-size: 6rem;
      opacity: 50%;
      z-index: -1; }
  .organizers__grid {
    margin: 8rem 40vw 0 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr); }
    .organizers__grid__box {
      position: relative;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center; }
      .organizers__grid__box > img {
        width: 70%;
        height: 70%; }
      .organizers__grid__box > a {
        position: absolute;
        left: 50%;
